body {
  font-family: Arial, Helvetica, sans-serif;
  padding: 60px;
}
input{
  font-size: 1.2rem;
  width: 100%;
  color: grey;
  margin-bottom: 20px;
}
.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p {
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 170px;
}
.cardGrid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}